import axios from 'axios';

const fetchReportData = async (tableName) => {
  try {
    const response = await axios({
      method: 'POST',
      url: `${process.env.REACT_APP_API_BASE_URL}/tables/${tableName}/Action`,
      headers: {
        'ApplicationAccessKey': process.env.REACT_APP_API_KEY,
        'Content-Type': 'application/json'
      },
      data: {
        Action: "Find",
        Properties: {
          Locale: "vi-VN",
          Timezone: "Asia/Ho_Chi_Minh", 
          Selector: `Filter(${tableName}, true)`
        },
        Rows: []
      }
    });
    
    return response.data;
  } catch (error) {
    console.error('API Error:', error);
    throw new Error('Không thể lấy dữ liệu');
  }
};

export { fetchReportData };