import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import {
  LineChart, Line, XAxis, YAxis, CartesianGrid, ResponsiveContainer,
  PieChart, Pie, Cell, BarChart, Bar, Tooltip, Legend
} from 'recharts';
import { CircularProgressbar } from 'react-circular-progressbar';
import { fetchReportData } from '../services/api';
import 'react-circular-progressbar/dist/styles.css';
import './Dashboard.css';

const CURRENT_DATE = new Date();

const COLORS = ['#0088FE', '#00C49F', '#FFBB28', '#FF8042', '#8884d8', '#82ca9d'];
const STATUS_COLORS = {
  active: '#4CAF50',
  pending: '#FFC107',
  completed: '#2196F3',
  cancelled: '#F44336'
};

function Dashboard() {
  const [data, setData] = useState({
    customers: [],
    quotations: [],
    pos: [],
    cskh: [],
    notifications: []
  });
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [timeRange, setTimeRange] = useState('6');
  const [searchTerm, setSearchTerm] = useState('');

  useEffect(() => {
    const fetchAllData = async () => {
      try {
        const [customers, quotations, pos, cskh, notifications] = await Promise.all([
          fetchReportData('DSKH'),
          fetchReportData('BAOGIA'),
          fetchReportData('PO'),
          fetchReportData('CSKH'),
          fetchReportData('THONGBAO')
        ]);

        setData({
          customers,
          quotations,
          pos,
          cskh,
          notifications: notifications.sort((a, b) =>
            new Date(b['Thời gian thông báo']) - new Date(a['Thời gian thông báo'])
          )
        });
      } catch (err) {
        setError('Không thể tải dữ liệu. Vui lòng thử lại sau.');
        console.error('Error fetching data:', err);
      } finally {
        setLoading(false);
      }
    };

    fetchAllData();
  }, []);

  const calculateMetrics = () => {
    const { customers, pos, quotations, cskh } = data;

    const currentMonth = CURRENT_DATE.getMonth();
    const currentYear = CURRENT_DATE.getFullYear();

    const currentMonthPOs = pos.filter(po => {
      const poDate = new Date(po.ngay_tao);
      return poDate.getMonth() === currentMonth && poDate.getFullYear() === currentYear;
    });

    const activeCustomers = customers.filter(c => c['Trạng thái'] === 'Đã thành khách hàng').length;
    const totalRevenue = currentMonthPOs.reduce((sum, po) => sum + (Number(po.thanh_tien) || 0), 0);
    const quotationSuccess = quotations.filter(q => q.trang_thai === 'Đã duyệt').length;

    return [
      {
        id: '01',
        value: customers.length,
        title: 'Tổng khách hàng',
        percentage: 100,
        trend: '+5.2%',
        color: '#0088FE'
      },
      {
        id: '02',
        value: activeCustomers,
        title: 'Khách hàng hiện tại',
        percentage: (activeCustomers / customers.length) * 100,
        trend: '+2.1%',
        color: '#00C49F'
      },
      {
        id: '03',
        value: quotations.length,
        title: 'Báo giá',
        percentage: (quotationSuccess / quotations.length) * 100,
        trend: '+3.7%',
        color: '#FFBB28'
      },
      {
        id: '04',
        value: formatCurrency(totalRevenue),
        title: 'Doanh thu tháng',
        percentage: 85,
        trend: '+12.5%',
        color: '#FF8042'
      },
      {
        id: '05',
        value: cskh.length,
        title: 'Lượt CSKH',
        percentage: (cskh.length / customers.length) * 100,
        trend: '+8.4%',
        color: '#8884d8'
      }
    ];
  };

  const prepareChartData = () => {
    const months = parseInt(timeRange);
    const monthLabels = [];
    const monthlyData = {
      revenue: {},
      cskh: {},
      customers: {}
    };

    // Generate last n months
    for (let i = months - 1; i >= 0; i--) {
      const date = new Date();
      date.setMonth(date.getMonth() - i);
      const monthKey = `${date.getMonth() + 1}/${date.getFullYear()}`;
      monthLabels.push(monthKey);
      monthlyData.revenue[monthKey] = 0;
      monthlyData.cskh[monthKey] = 0;
      monthlyData.customers[monthKey] = 0;
    }

    // Fill revenue data
    data.pos.forEach(po => {
      const date = new Date(po.ngay_tao);
      const monthKey = `${date.getMonth() + 1}/${date.getFullYear()}`;
      if (monthlyData.revenue[monthKey] !== undefined) {
        monthlyData.revenue[monthKey] += Number(po.thanh_tien || 0);
      }
    });

    // Fill CSKH data - sử dụng Ngày thực hiện
    data.cskh.forEach(item => {
      const date = new Date(item['Ngày thực hiện']);
      const monthKey = `${date.getMonth() + 1}/${date.getFullYear()}`;
      if (monthlyData.cskh[monthKey] !== undefined) {
        monthlyData.cskh[monthKey]++;
      }
    });

    // Fill customer data - sử dụng Ngày ghi nhận
    data.customers.forEach(customer => {
      const date = new Date(customer['Ngày ghi nhân']);
      const monthKey = `${date.getMonth() + 1}/${date.getFullYear()}`;
      if (monthlyData.customers[monthKey] !== undefined) {
        monthlyData.customers[monthKey]++;
      }
    });

    return {
      revenueData: monthLabels.map(month => ({
        month,
        value: monthlyData.revenue[month]
      })),
      cskhData: monthLabels.map(month => ({
        month,
        value: monthlyData.cskh[month]
      })),
      customerData: monthLabels.map(month => ({
        month,
        value: monthlyData.customers[month]
      }))
    };
  };
  // Thêm hàm phân tích dữ liệu theo nhân viên
  const prepareStaffStats = () => {
    const staffStats = {};

    // Xử lý dữ liệu khách hàng
    data.customers.forEach(customer => {
      const staff = customer['Nhân viên phụ trách'] || 'Chưa phân công';
      if (!staffStats[staff]) {
        staffStats[staff] = {
          name: staff,
          customers: 0,
          quotations: 0,
          orders: 0,
          revenue: 0,
          cskh: 0
        };
      }
      staffStats[staff].customers++;
    });

    // Xử lý dữ liệu báo giá
    data.quotations.forEach(quote => {
      const staff = quote.nhan_vien_phu_trach || 'Chưa phân công';
      if (!staffStats[staff]) {
        staffStats[staff] = {
          name: staff,
          customers: 0,
          quotations: 0,
          orders: 0,
          revenue: 0,
          cskh: 0
        };
      }
      staffStats[staff].quotations++;
    });

    // Xử lý dữ liệu đơn hàng và doanh số
    data.pos.forEach(po => {
      const staff = po.nhan_vien_phu_trach || 'Chưa phân công';
      if (!staffStats[staff]) {
        staffStats[staff] = {
          name: staff,
          customers: 0,
          quotations: 0,
          orders: 0,
          revenue: 0,
          cskh: 0
        };
      }
      staffStats[staff].orders++;
      staffStats[staff].revenue += Number(po.thanh_tien) || 0;
    });

    // Xử lý dữ liệu CSKH
    data.cskh.forEach(item => {
      const staff = item['Nhân viên thực hiện'] || 'Chưa phân công';
      if (!staffStats[staff]) {
        staffStats[staff] = {
          name: staff,
          customers: 0,
          quotations: 0,
          orders: 0,
          revenue: 0,
          cskh: 0
        };
      }
      staffStats[staff].cskh++;
    });

    return Object.values(staffStats);
  };
  const prepareQuotationStats = () => {
    const statusCount = {};
    data.quotations.forEach(quote => {
      const status = quote.trang_thai || 'Chưa xác định';
      statusCount[status] = (statusCount[status] || 0) + 1;
    });
    return Object.entries(statusCount).map(([name, value]) => ({ name, value }));
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-screen bg-gray-50">
        <div className="text-center">
          <div className="animate-spin rounded-full h-16 w-16 border-b-2 border-blue-500 mx-auto"></div>
          <p className="mt-4 text-gray-600">Đang tải dữ liệu...</p>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="flex items-center justify-center min-h-screen bg-gray-50">
        <div className="text-center text-red-500">
          <svg className="w-16 h-16 mx-auto mb-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 8v4m0 4h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
          </svg>
          <p className="text-xl font-semibold">{error}</p>
        </div>
      </div>
    );
  }

  const metrics = calculateMetrics();
  const { revenueData, cskhData, customerData } = prepareChartData();
  const quotationStats = prepareQuotationStats();

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Navigation Bar */}
      <nav className="bg-white shadow-sm px-6 py-4 sticky top-0 z-10">
        <div className=" mx-auto flex justify-between items-center">
          <div>
            <h1 className="text-3xl font-bold bg-gradient-to-r from-amber-600 to-yellow-500 bg-clip-text text-transparent">
              TAT ERP Dashboard
            </h1>
            <p className="text-gray-500 text-sm">
              {CURRENT_DATE.toLocaleString('vi-VN', {
                dateStyle: 'full',
                timeStyle: 'short'
              })}
            </p>
          </div>

          <div className="flex items-center space-x-4">

            <a className="p-2 rounded-full hover:bg-gray-100 transition-colors relative" href="/notifications" >

              <svg className="w-6 h-6 text-gray-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path d="M15 17h5l-1.405-1.405A2.032 2.032 0 0118 14.158V11a6.002 6.002 0 00-4-5.659V5a2 2 0 10-4 0v.341C7.67 6.165 6 8.388 6 11v3.159c0 .538-.214 1.055-.595 1.436L4 17h5m6 0v1a3 3 0 11-6 0v-1m6 0H9" strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" />
              </svg>
              <span className="absolute top-0 right-0 h-4 w-4 bg-red-500 rounded-full text-xs text-white flex items-center justify-center">
                {data.notifications.filter(n => !n.read).length}
              </span>
            </a>
          </div>
        </div>
      </nav>

      {/* Main Content */}
      <main className=" mx-auto p-6">
        {/* Metrics Grid */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-5 gap-6 mb-6">
          {metrics.map((metric) => (
            <div
              key={metric.id}
              className="bg-white rounded-xl shadow-sm hover:shadow-md transition-all duration-300 transform hover:-translate-y-1"
            >
              <div className="p-6">
                <div className="flex items-center justify-between mb-4">
                  <h3 className="text-gray-700 font-medium">{metric.title}</h3>
                  <span className={`text-sm ${metric.trend.startsWith('+') ? 'text-green-500' : 'text-red-500'}`}>
                    {metric.trend}
                  </span>
                </div>

                <div className="w-24 h-24 mx-auto mb-4">
                  <CircularProgressbar
                    value={metric.percentage}
                    text={typeof metric.value === 'number' ?
                      metric.title.includes('Doanh thu') ?
                        formatCurrency(metric.value).replace('₫', '') :
                        metric.value.toLocaleString()
                      : metric.value}
                    styles={{
                      path: {
                        stroke: metric.color,
                        strokeLinecap: 'round',
                        transition: 'stroke-dashoffset 0.5s ease 0s'
                      },
                      trail: {
                        stroke: '#f3f4f6'
                      },
                      text: {
                        fill: metric.color,
                        fontSize: '16px',
                        fontWeight: 'bold'
                      }
                    }}
                  />
                </div>

                <div className="text-center text-sm text-gray-500">
                  {metric.percentage.toFixed(1)}% hoàn thành
                </div>
              </div>
            </div>
          ))}
        </div>
        {/* Staff Performance Table */}
        <div className="mt-6 bg-white rounded-xl shadow-sm hover:shadow-md transition-all duration-300">
          <div className="p-6">
            <h2 className="text-xl font-semibold text-gray-800 mb-6">Thống kê theo nhân viên</h2>
            <div className="overflow-x-auto">
              <table className="min-w-full divide-y divide-gray-200">
                <thead className="bg-gray-50">
                  <tr>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Nhân viên
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Khách hàng phụ trách
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Số lần báo giá
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Đơn hàng
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Doanh số
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Số lần CSKH
                    </th>
                  </tr>
                </thead>
                <tbody className="bg-white divide-y divide-gray-200">
                  {prepareStaffStats().map((staff, index) => (
                    <tr key={index} className="hover:bg-gray-50">
                      <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900">
                        {staff.name}
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                        {staff.customers}
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                        {staff.quotations}
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                        {staff.orders}
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                        {formatCurrency(staff.revenue)}
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                        {staff.cskh}
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </div>
        </div>

        {/* Staff Distribution Chart */}
        <div className="mt-6 grid grid-cols-1 lg:grid-cols-2 gap-6">
          <div className="bg-white rounded-xl shadow-sm hover:shadow-md transition-all duration-300">
            <div className="p-6">
              <h2 className="text-xl font-semibold text-gray-800 mb-6">Phân bổ khách hàng theo nhân viên</h2>
              <div className="h-80">
                <ResponsiveContainer width="100%" height="100%">
                  <PieChart>
                    <Pie
                      data={prepareStaffStats()}
                      dataKey="customers"
                      nameKey="name"
                      cx="50%"
                      cy="50%"
                      outerRadius={100}
                      label={({ name, value }) => `${name}: ${value}`}
                    >
                      {prepareStaffStats().map((entry, index) => (
                        <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
                      ))}
                    </Pie>
                    <Tooltip />
                    <Legend />
                  </PieChart>
                </ResponsiveContainer>
              </div>
            </div>
          </div>

          <div className="bg-white rounded-xl shadow-sm hover:shadow-md transition-all duration-300">
            <div className="p-6">
              <h2 className="text-xl font-semibold text-gray-800 mb-6">Doanh số theo nhân viên</h2>
              <div className="h-80">
                <ResponsiveContainer width="100%" height="100%">
                  <BarChart data={prepareStaffStats()}>
                    <CartesianGrid strokeDasharray="3 3" stroke="#f0f0f0" />
                    <XAxis dataKey="name" />
                    <YAxis
                      tickFormatter={(value) => {
                        if (value >= 1000000) {
                          return `${(value / 1000000).toFixed(1)}M`;
                        }
                        return value;
                      }}
                    />
                    <Tooltip formatter={(value) => formatCurrency(value)} />
                    <Legend />
                    <Bar dataKey="revenue" name="Doanh số" fill="#8884d8" />
                  </BarChart>
                </ResponsiveContainer>
              </div>
            </div>
          </div>
        </div>
        {/* Charts Grid */}
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
          {/* Revenue Chart */}
          <div className="bg-white rounded-xl shadow-sm hover:shadow-md transition-all duration-300">
            <div className="p-6">
              <div className="flex justify-between items-center mb-6">
                <h2 className="text-xl font-semibold text-gray-800">Xu hướng doanh thu</h2>
                <select
                  className="text-sm border-gray-200 rounded-lg focus:ring-blue-500"
                  value={timeRange}
                  onChange={(e) => setTimeRange(e.target.value)}
                >
                  <option value="6">6 tháng gần nhất</option>
                  <option value="12">12 tháng gần nhất</option>
                </select>
              </div>

              <div className="h-80">
                <ResponsiveContainer width="100%" height="100%">
                  <LineChart data={revenueData}>
                    <CartesianGrid strokeDasharray="3 3" stroke="#f0f0f0" />
                    <XAxis dataKey="month" />
                    <YAxis
                      tickFormatter={(value) => {
                        if (value >= 1000000) {
                          return `${(value / 1000000).toFixed(1)}M`;
                        }
                        return value;
                      }}
                    />
                    <Tooltip formatter={(value) => formatCurrency(value)} />
                    <Legend />
                    <Line
                      type="monotone"
                      dataKey="value"
                      name="Doanh thu"
                      stroke="#3b82f6"
                      strokeWidth={2}
                      dot={{ r: 4 }}
                      activeDot={{ r: 6 }}
                    />
                  </LineChart>
                </ResponsiveContainer>
              </div>
            </div>
          </div>

          {/* CSKH Chart */}
          <div className="bg-white rounded-xl shadow-sm hover:shadow-md transition-all duration-300">
            <div className="p-6">
              <div className="flex justify-between items-center mb-6">
                <h2 className="text-xl font-semibold text-gray-800">Xu hướng CSKH</h2>
                <select
                  className="text-sm border-gray-200 rounded-lg focus:ring-blue-500"
                  value={timeRange}
                  onChange={(e) => setTimeRange(e.target.value)}
                >
                  <option value="6">6 tháng gần nhất</option>
                  <option value="12">12 tháng gần nhất</option>
                </select>
              </div>

              <div className="h-80">
                <ResponsiveContainer width="100%" height="100%">
                  <LineChart data={cskhData}>
                    <CartesianGrid strokeDasharray="3 3" stroke="#f0f0f0" />
                    <XAxis dataKey="month" />
                    <YAxis />
                    <Tooltip />
                    <Legend />
                    <Line
                      type="monotone"
                      dataKey="value"
                      name="Số lượt CSKH"
                      stroke="#00C49F"
                      strokeWidth={2}
                      dot={{ r: 4 }}
                      activeDot={{ r: 6 }}
                    />
                  </LineChart>
                </ResponsiveContainer>
              </div>
            </div>
          </div>

          {/* Customer Growth Chart */}
          <div className="bg-white rounded-xl shadow-sm hover:shadow-md transition-all duration-300">
            <div className="p-6">
              <div className="flex justify-between items-center mb-6">
                <h2 className="text-xl font-semibold text-gray-800">Tăng trưởng khách hàng</h2>
                <select
                  className="text-sm border-gray-200 rounded-lg focus:ring-blue-500"
                  value={timeRange}
                  onChange={(e) => setTimeRange(e.target.value)}
                >
                  <option value="6">6 tháng gần nhất</option>
                  <option value="12">12 tháng gần nhất</option>
                </select>
              </div>

              <div className="h-80">
                <ResponsiveContainer width="100%" height="100%">
                  <LineChart data={customerData}>
                    <CartesianGrid strokeDasharray="3 3" stroke="#f0f0f0" />
                    <XAxis dataKey="month" />
                    <YAxis />
                    <Tooltip />
                    <Legend />
                    <Line
                      type="monotone"
                      dataKey="value"
                      name="Khách hàng mới"
                      stroke="#8884d8"
                      strokeWidth={2}
                      dot={{ r: 4 }}
                      activeDot={{ r: 6 }}
                    />
                  </LineChart>
                </ResponsiveContainer>
              </div>
            </div>
          </div>

          {/* Quotation Status Chart */}
          <div className="bg-white rounded-xl shadow-sm hover:shadow-md transition-all duration-300">
            <div className="p-6">
              <div className="flex justify-between items-center mb-6">
                <h2 className="text-xl font-semibold text-gray-800">Trạng thái báo giá</h2>
              </div>

              <div className="h-80">
                <ResponsiveContainer width="100%" height="100%">
                  <BarChart data={quotationStats}>
                    <CartesianGrid strokeDasharray="3 3" stroke="#f0f0f0" />
                    <XAxis dataKey="name" />
                    <YAxis />
                    <Tooltip />
                    <Legend />
                    <Bar dataKey="value" name="Số lượng">
                      {quotationStats.map((entry, index) => (
                        <Cell
                          key={`cell-${index}`}
                          fill={STATUS_COLORS[entry.name.toLowerCase()] || COLORS[index % COLORS.length]}
                        />
                      ))}
                    </Bar>
                  </BarChart>
                </ResponsiveContainer>
              </div>
            </div>
          </div>
        </div>
      </main>
    </div>
  );
}

// Helper function for currency formatting
const formatCurrency = (value) => {
  if (value >= 1000000) {
    return new Intl.NumberFormat('vi-VN', {
      style: 'currency',
      currency: 'VND',
      notation: 'compact',
      maximumFractionDigits: 1
    }).format(value);
  }
  return new Intl.NumberFormat('vi-VN', {
    style: 'currency',
    currency: 'VND'
  }).format(value);
};

export default Dashboard;