import React, { useState, useEffect } from 'react';
import { fetchReportData } from '../services/api';
import {
    BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, Legend,
    PieChart, Pie, Cell,
    LineChart, Line, ResponsiveContainer
} from 'recharts';

// Custom Card Component
const Card = ({ children, className = "" }) => (
    <div className={`bg-white rounded-lg shadow-sm ${className}`}>
        {children}
    </div>
);

// Icons as SVG components
const Icons = {
    Search: () => (
        <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
        </svg>
    ),
    Users: () => (
        <svg xmlns="http://www.w3.org/2000/svg" className="h-8 w-8" fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 4.354a4 4 0 110 5.292M15 21H3v-1a6 6 0 0112 0v1zm0 0h6v-1a6 6 0 00-9-5.197M13 7a4 4 0 11-8 0 4 4 0 018 0z" />
        </svg>
    ),
    Wallet: () => (
        <svg xmlns="http://www.w3.org/2000/svg" className="h-8 w-8" fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 10h18M7 15h1m4 0h1m-7 4h12a3 3 0 003-3V8a3 3 0 00-3-3H6a3 3 0 00-3 3v8a3 3 0 003 3z" />
        </svg>
    ),
    CreditCard: () => (
        <svg xmlns="http://www.w3.org/2000/svg" className="h-8 w-8" fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 10h18M7 15h1m4 0h1m-7 4h12a3 3 0 003-3V8a3 3 0 00-3-3H6a3 3 0 00-3 3v8a3 3 0 003 3z" />
        </svg>
    ),
    TrendingUp: () => (
        <svg xmlns="http://www.w3.org/2000/svg" className="h-8 w-8" fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M13 7h8m0 0v8m0-8l-8 8-4-4-6 6" />
        </svg>
    ),
    Close: () => (
        <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
        </svg>
    )
};

function CustomerDebt() {
    const [data, setData] = useState({
        customers: [],
        pos: [],
        payments: []
    });
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const [searchTerm, setSearchTerm] = useState('');
    const [selectedCustomer, setSelectedCustomer] = useState(null);
    const [currentPage, setCurrentPage] = useState(1);
    const [itemsPerPage] = useState(10);

    useEffect(() => {
        const fetchAllData = async () => {
            try {
                const [customers, pos, payments] = await Promise.all([
                    fetchReportData('DSKH'),
                    fetchReportData('PO'),
                    fetchReportData('LSTHANHTOANKH')
                ]);

                setData({
                    customers,
                    pos,
                    payments: payments.sort((a, b) =>
                        new Date(b.Ngay_TT) - new Date(a.Ngay_TT)
                    )
                });
            } catch (err) {
                setError('Không thể tải dữ liệu. Vui lòng thử lại sau.');
                console.error('Error fetching data:', err);
            } finally {
                setLoading(false);
            }
        };

        fetchAllData();
    }, []);
    const refreshData = async () => {
        setLoading(true);
        try {
            const [customers, pos, payments] = await Promise.all([
                fetchReportData('DSKH'),
                fetchReportData('PO'),
                fetchReportData('LSTHANHTOANKH')
            ]);

            setData({
                customers,
                pos,
                payments: payments.sort((a, b) =>
                    new Date(b.Ngay_TT) - new Date(a.Ngay_TT)
                )
            });
        } catch (err) {
            setError('Không thể tải dữ liệu. Vui lòng thử lại sau.');
        } finally {
            setLoading(false);
        }
    };
    // Tính toán công nợ cho từng khách hàng
    const calculateCustomerDebts = () => {
        return data.customers.map(customer => {
            const totalOrders = data.pos
                .filter(po => po.id_khach_hang === customer.IDKH)
                .reduce((sum, po) => sum + (Number(po.thanh_tien) || 0), 0);

            const totalPaid = data.payments
                .filter(payment => payment.Ten_KH === customer.IDKH || payment.id_khach_hang === customer.IDKH)
                .reduce((sum, payment) => sum + (Number(payment.So_tien_TT) || 0), 0);

            return {
                ...customer,
                totalOrders,
                totalPaid,
                remainingDebt: totalOrders - totalPaid
            };
        });
    };

    // Tính toán công nợ cho từng đơn hàng của khách hàng
    const calculateOrderDebts = (customerId) => {
        return data.pos
            .filter(po => po.id_khach_hang === customerId)
            .map(po => {
                const paidForOrder = data.payments
                    .filter(payment => payment.So_DH === po.id_don_hang)
                    .reduce((sum, payment) => sum + (Number(payment.So_tien_TT) || 0), 0);

                return {
                    ...po,
                    paidAmount: paidForOrder,
                    remainingDebt: Number(po.thanh_tien) - paidForOrder
                };
            });
    };

    // Lọc khách hàng theo từ khóa tìm kiếm
    const filteredCustomers = calculateCustomerDebts().filter(customer =>
        customer['Tên khách hàng'].toLowerCase().includes(searchTerm.toLowerCase()) ||
        customer['IDKH']?.toLowerCase().includes(searchTerm.toLowerCase())
    );

    // Tính toán phân trang
    const indexOfLastItem = currentPage * itemsPerPage;
    const indexOfFirstItem = indexOfLastItem - itemsPerPage;
    const currentItems = filteredCustomers.slice(indexOfFirstItem, indexOfLastItem);
    const totalPages = Math.ceil(filteredCustomers.length / itemsPerPage);

    const formatCurrency = (value) => {
        return new Intl.NumberFormat('vi-VN', {
            style: 'currency',
            currency: 'VND'
        }).format(value);
    };

    const formatDate = (date) => {
        return new Date(date).toLocaleDateString('vi-VN');
    };

    if (loading) {
        return (
            <div className="flex items-center justify-center h-screen">
                <div className="flex flex-col items-center gap-4">
                    <div className="animate-spin rounded-full h-12 w-12 border-4 border-blue-500 border-t-transparent"></div>
                    <p className="text-gray-600">Đang tải dữ liệu...</p>
                </div>
            </div>
        );
    }

    if (error) {
        return (
            <div className="flex items-center justify-center h-screen">
                <div className="bg-red-50 p-6 rounded-lg shadow-lg">
                    <h3 className="text-red-800 font-medium mb-2">Lỗi</h3>
                    <p className="text-red-600">{error}</p>
                    <button
                        onClick={() => window.location.reload()}
                        className="mt-4 px-4 py-2 bg-red-600 text-white rounded-lg hover:bg-red-700 transition-colors"
                    >
                        Thử lại
                    </button>
                </div>
            </div>
        );
    }

    return (
        <div className="min-h-screen bg-gray-50 p-6">
            <div className=" mx-auto">
                {/* Header Section */}
                <div className="mb-8 flex justify-between items-center">
                    <div>
                        <h1 className="text-3xl font-bold text-gray-900">Quản lý công nợ</h1>
                        <p className="text-gray-600 mt-2">Theo dõi và quản lý công nợ khách hàng</p>
                    </div>
                    <button
                        onClick={refreshData}
                        className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors flex items-center gap-2"
                        disabled={loading}
                    >
                        {loading ? (
                            <div className="animate-spin h-5 w-5 border-2 border-white border-t-transparent rounded-full"></div>
                        ) : (
                            <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
                                <path fillRule="evenodd" d="M4 2a1 1 0 011 1v2.101a7.002 7.002 0 0111.601 2.566 1 1 0 11-1.885.666A5.002 5.002 0 005.999 7H9a1 1 0 010 2H4a1 1 0 01-1-1V3a1 1 0 011-1zm.008 9.057a1 1 0 011.276.61A5.002 5.002 0 0014.001 13H11a1 1 0 110-2h5a1 1 0 011 1v4a1 1 0 11-2 0v-2.101a7.002 7.002 0 01-11.601-2.566 1 1 0 01.61-1.276z" clipRule="evenodd" />
                            </svg>
                        )}
                        Làm mới
                    </button>
                </div>

                {/* Stats Cards */}
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-8">
                    <Card className="bg-gradient-to-br from-teal-500 to-teal-600">
                        <div className="p-6">
                            <div className="flex items-center justify-between">
                                <div>
                                    <p className="text-white text-sm">Tổng khách hàng</p>
                                    <h3 className="text-white text-2xl font-bold mt-2">
                                        {filteredCustomers.length}
                                    </h3>
                                </div>
                                <div className="text-white/80">
                                    <Icons.Users />
                                </div>
                            </div>
                        </div>
                    </Card>

                    <Card className="bg-gradient-to-br from-sky-500 to-sky-600">
                        <div className="p-6">
                            <div className="flex items-center justify-between">
                                <div>
                                    <p className="text-white text-sm">Đã thanh toán</p>
                                    <h3 className="text-white text-2xl font-bold mt-2">
                                        {formatCurrency(filteredCustomers.reduce((sum, c) => sum + c.totalPaid, 0))}
                                    </h3>
                                </div>
                                <div className="text-white/80">
                                    <Icons.Wallet />
                                </div>
                            </div>
                        </div>
                    </Card>

                    <Card className="bg-gradient-to-br from-rose-500 to-rose-600">
                        <div className="p-6">
                            <div className="flex items-center justify-between">
                                <div>
                                    <p className="text-white text-sm">Tổng công nợ</p>
                                    <h3 className="text-white text-2xl font-bold mt-2">
                                        {formatCurrency(filteredCustomers.reduce((sum, c) => sum + c.remainingDebt, 0))}
                                    </h3>
                                </div>
                                <div className="text-white/80">
                                    <Icons.CreditCard />
                                </div>
                            </div>
                        </div>
                    </Card>

                    <Card className="bg-gradient-to-br from-emerald-300 to-emerald-400">
                        <div className="p-6">
                            <div className="flex items-center justify-between">
                                <div>
                                    <p className="text-white text-sm">Số đơn hàng</p>
                                    <h3 className="text-white text-2xl font-bold mt-2">{data.pos.length}</h3>
                                </div>
                                <div className="text-white/80">
                                    <Icons.TrendingUp />
                                </div>
                            </div>
                        </div>
                    </Card>
                </div>

                {/* Search Section */}
                <div className="mb-8">
                    <div className="relative">
                        <input
                            type="search"
                            placeholder="Tìm kiếm khách hàng..."
                            className="w-full pl-12 pr-4 py-3 bg-white rounded-xl shadow-sm border border-gray-200 focus:border-blue-500 focus:ring-2 focus:ring-blue-200 transition-all"
                            value={searchTerm}
                            onChange={(e) => setSearchTerm(e.target.value)}
                        />
                        <div className="absolute left-4 top-3.5 text-gray-400">
                            <Icons.Search />
                        </div>
                    </div>
                </div>

                {/* Charts Grid */}
                <div className="grid grid-cols-1 lg:grid-cols-2 gap-8 mb-8">
       
<Card>
    <div className="p-6">
        <h3 className="text-lg font-semibold mb-6">Top khách hàng còn nợ</h3>
        <div className="overflow-hidden">
            <ResponsiveContainer width="100%" height={300}>
                <BarChart
                    data={filteredCustomers
                        .sort((a, b) => b.remainingDebt - a.remainingDebt)
                        .slice(0, 5)}
                    margin={{ top: 5, right: 30, left: 20, bottom: 5 }}
                >
                    <CartesianGrid strokeDasharray="3 3" />
                    <XAxis dataKey="Tên khách hàng" />
                    <YAxis />
                    <Tooltip formatter={(value) => formatCurrency(value)} />
                    <Bar dataKey="remainingDebt" fill="#f43f5e" name="Công nợ" />
                </BarChart>
            </ResponsiveContainer>
        </div>
    </div>
</Card>


<Card>
    <div className="p-6">
        <h3 className="text-lg font-semibold mb-6">Tỷ lệ thanh toán</h3>
        <ResponsiveContainer width="100%" height={300}>
            <PieChart>
                <Pie
                    data={[
                        {
                            name: 'Đã thanh toán',
                            value: filteredCustomers.reduce((sum, c) => sum + c.totalPaid, 0)
                        },
                        {
                            name: 'Còn nợ',
                            value: filteredCustomers.reduce((sum, c) => sum + c.remainingDebt, 0)
                        }
                    ]}
                    cx="50%"
                    cy="50%"
                    innerRadius={60}
                    outerRadius={100}
                    fill="#8884d8"
                    paddingAngle={5}
                    dataKey="value"
                >
                    <Cell fill="#0ea5e9" />
                    <Cell fill="#f43f5e" />
                </Pie>
                <Tooltip formatter={(value) => formatCurrency(value)} />
                <Legend />
            </PieChart>
        </ResponsiveContainer>
    </div>
</Card>

                    <Card className="col-span-1 lg:col-span-2">
                        <div className="p-6">
                            <h3 className="text-lg font-semibold">Xu hướng thanh toán</h3>
                            <ResponsiveContainer width="100%" height={300}>
                                <LineChart
                                    data={data.payments
                                        .sort((a, b) => new Date(a.Ngay_TT) - new Date(b.Ngay_TT))
                                        .reduce((acc, payment) => {
                                            const date = new Date(payment.Ngay_TT).toLocaleDateString();
                                            const existingDate = acc.find(item => item.date === date);
                                            if (existingDate) {
                                                existingDate.amount += Number(payment.So_tien_TT);
                                            } else {
                                                acc.push({ date, amount: Number(payment.So_tien_TT) });
                                            }
                                            return acc;
                                        }, [])}
                                    margin={{ top: 5, right: 30, left: 20, bottom: 5 }}
                                >
                                    <CartesianGrid strokeDasharray="3 3" />
                                    <XAxis dataKey="date" />
                                    <YAxis />
                                    <Tooltip formatter={(value) => formatCurrency(value)} />
                                    <Legend />
                                    <Line type="monotone" dataKey="amount" stroke="#3b82f6" name="Số tiền thanh toán" />
                                </LineChart>
                            </ResponsiveContainer>
                        </div>
                    </Card>
                </div>

                {/* Main Table */}
                <Card className="mb-8">
                    <div className="overflow-x-auto">
                        <table className="w-full">
                            <thead>
                                <tr className="bg-gray-50 border-b border-gray-200">
                                    <th className="px-6 py-4 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">
                                        Khách hàng
                                    </th>
                                    <th className="px-6 py-4 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">
                                        Tổng đơn hàng
                                    </th>
                                    <th className="px-6 py-4 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">
                                        Đã thanh toán
                                    </th>
                                    <th className="px-6 py-4 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">
                                        Còn nợ
                                    </th>
                                    <th className="px-6 py-4 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">
                                        Thao tác
                                    </th>
                                </tr>
                            </thead>
                            <tbody className="divide-y divide-gray-200">
                                {currentItems.map((customer) => (
                                    <tr key={customer.ID} className="hover:bg-gray-50 transition-colors">
                                        <td className="px-6 py-4">
                                            <div className="text-sm font-medium text-gray-900">
                                                {customer['Tên khách hàng']}
                                            </div>
                                            <div className="text-sm text-gray-500">{customer.IDKH}</div>
                                        </td>
                                        <td className="px-6 py-4 text-sm text-gray-600">
                                            {formatCurrency(customer.totalOrders)}
                                        </td>
                                        <td className="px-6 py-4 text-sm text-gray-600">
                                            {formatCurrency(customer.totalPaid)}
                                        </td>
                                        <td className="px-6 py-4">
                                            <span className={`inline-flex items-center px-3 py-1 rounded-full text-sm font-medium ${customer.remainingDebt > 0
                                                ? 'bg-red-100 text-red-800'
                                                : 'bg-green-100 text-green-800'
                                                }`}>
                                                {formatCurrency(customer.remainingDebt)}
                                            </span>
                                        </td>
                                        <td className="px-6 py-4">
                                            <button
                                                onClick={() => setSelectedCustomer(customer)}
                                                className="text-blue-600 hover:text-blue-800 font-medium transition-colors"
                                            >
                                                Chi tiết
                                            </button>
                                        </td>
                                    </tr>
                                ))}
                            </tbody>
                        </table>
                    </div>
                </Card>

                {/* Pagination */}
                <div className="flex items-center justify-between py-4">
                    <div className="text-sm text-gray-600">
                        Hiển thị {indexOfFirstItem + 1} đến {Math.min(indexOfLastItem, filteredCustomers.length)} trong số {filteredCustomers.length} khách hàng
                    </div>
                    <div className="flex gap-2">
                        <button
                            onClick={() => setCurrentPage(p => Math.max(1, p - 1))}
                            disabled={currentPage === 1}
                            className="px-4 py-2 border rounded-lg disabled:opacity-50 disabled:cursor-not-allowed hover:bg-gray-50 transition-colors"
                        >
                            Trước
                        </button>
                        {Array.from({ length: totalPages }, (_, i) => i + 1).map(page => (
                            <button
                                key={page}
                                onClick={() => setCurrentPage(page)}
                                className={`px-4 py-2 rounded-lg transition-colors ${currentPage === page
                                    ? 'bg-blue-600 text-white'
                                    : 'border hover:bg-gray-50'
                                    }`}
                            >
                                {page}
                            </button>
                        ))}
                        <button
                            onClick={() => setCurrentPage(p => Math.min(totalPages, p + 1))}
                            disabled={currentPage === totalPages}
                            className="px-4 py-2 border rounded-lg disabled:opacity-50 disabled:cursor-not-allowed hover:bg-gray-50 transition-colors"
                        >
                            Sau
                        </button>
                    </div>
                </div>

                {/* Modal */}
                {selectedCustomer && (
                    <div className="fixed inset-0 bg-black/50 backdrop-blur-sm flex items-center justify-center z-50">
                        <div className="bg-white rounded-xl max-w-4xl w-full max-h-[90vh] overflow-hidden">
                            <div className="flex flex-col h-full">
                                {/* Modal Header */}
                                <div className="flex items-center justify-between p-6 border-b border-gray-200">
                                    <h2 className="text-xl font-semibold text-gray-900">
                                        Chi tiết thanh toán - {selectedCustomer['Tên khách hàng']}
                                    </h2>
                                    <button
                                        onClick={() => setSelectedCustomer(null)}
                                        className="text-gray-400 hover:text-gray-600 transition-colors"
                                    >
                                        <Icons.Close />
                                    </button>
                                </div>

                                {/* Modal Content */}
                                <div className="flex-1 overflow-y-auto p-6">
                                    <div className="space-y-8">
                                        {/* Đơn hàng và công nợ */}
                                        <div>
                                            <h3 className="text-lg font-semibold mb-4">Đơn hàng và công nợ</h3>
                                            <div className="bg-white rounded-lg border border-gray-200 overflow-hidden">
                                                <table className="min-w-full divide-y divide-gray-200">
                                                    <thead className="bg-gray-50">
                                                        <tr>
                                                            <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Số ĐH</th>
                                                            <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Ngày</th>
                                                            <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Giá trị ĐH</th>
                                                            <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Đã thanh toán</th>
                                                            <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Còn nợ</th>
                                                        </tr>
                                                    </thead>
                                                    <tbody className="divide-y divide-gray-200">
                                                        {calculateOrderDebts(selectedCustomer.IDKH).map(order => (
                                                            <tr key={order.id_don_hang} className="hover:bg-gray-50">
                                                                <td className="px-6 py-4 text-sm text-gray-900">{order['Số đơn hàng']}</td>
                                                                <td className="px-6 py-4 text-sm text-gray-600">{formatDate(order.ngay_tao)}</td>
                                                                <td className="px-6 py-4 text-sm text-gray-900 font-medium">{formatCurrency(order.thanh_tien)}</td>
                                                                <td className="px-6 py-4 text-sm text-gray-900">{formatCurrency(order.paidAmount)}</td>
                                                                <td className="px-6 py-4">
                                                                    <span className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium ${order.remainingDebt > 0
                                                                        ? 'bg-red-100 text-red-800'
                                                                        : 'bg-green-100 text-green-800'
                                                                        }`}>
                                                                        {formatCurrency(order.remainingDebt)}
                                                                    </span>
                                                                </td>
                                                            </tr>
                                                        ))}
                                                    </tbody>
                                                </table>
                                            </div>
                                        </div>

                                        {/* Lịch sử thanh toán */}
                                        <div>
                                            <h3 className="text-lg font-semibold mb-4">Lịch sử thanh toán</h3>
                                            <div className="bg-white rounded-lg border border-gray-200 overflow-hidden">
                                                <table className="min-w-full divide-y divide-gray-200">
                                                    <thead className="bg-gray-50">
                                                        <tr>
                                                            <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Số ĐH</th>
                                                            <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Ngày TT</th>
                                                            <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Số tiền</th>
                                                            <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Hình thức</th>
                                                            <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Người nhận</th>
                                                            <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Trạng thái</th>
                                                        </tr>
                                                    </thead>
                                                    <tbody className="divide-y divide-gray-200">
                                                        {data.payments
                                                            .filter(payment => payment['Ten_KH'] === selectedCustomer['IDKH'])
                                                            .map(payment => (
                                                                <tr key={payment.ID_LSTT} className="hover:bg-gray-50">
                                                                    <td className="px-6 py-4 text-sm text-gray-900">{payment.So_DH}</td>
                                                                    <td className="px-6 py-4 text-sm text-gray-600">{formatDate(payment.Ngay_TT)}</td>
                                                                    <td className="px-6 py-4 text-sm text-gray-900 font-medium">{formatCurrency(payment.So_tien_TT)}</td>
                                                                    <td className="px-6 py-4 text-sm text-gray-900">{payment.Hinh_thuc_TT}</td>
                                                                    <td className="px-6 py-4 text-sm text-gray-600">{payment.Nguoi_nhan}</td>
                                                                    <td className="px-6 py-4">
                                                                        <span className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium ${payment.Trang_thai === 'Đã thanh toán'
                                                                            ? 'bg-green-100 text-green-800'
                                                                            : 'bg-red-100 text-red-800'
                                                                            }`}>
                                                                            {payment.Trang_thai}
                                                                        </span>
                                                                    </td>
                                                                </tr>
                                                            ))}
                                                    </tbody>
                                                </table>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                )}
            </div>
        </div>
    );
}

export default CustomerDebt;