import React from 'react';
import { Link, useLocation } from 'react-router-dom';
import { AiOutlineHome, AiOutlineDashboard, AiOutlineUser, AiOutlineMessage, AiOutlineSetting, AiOutlineLogout } from 'react-icons/ai';

function Layout({ children }) {
  const location = useLocation();
  
  const menuItems = [
    { icon: <AiOutlineHome className="w-6 h-6" />, text: 'Home', path: '/' },
    { icon: <AiOutlineMessage className="w-6 h-6" />, text: 'Messages', path: '/notifications' },
    { icon: <AiOutlineSetting className="w-6 h-6" />, text: 'Công nợ', path: '/customer-debt' },
  
  ];

  return (
    <div className="flex h-screen bg-gray-50">
      {/* Sidebar */}
      <div className="w-20 bg-white shadow-lg">
        <div className="flex flex-col items-center py-4 space-y-8">
          <img src="https://thaianhtuan.com.vn/wp-content/uploads/2023/10/z4791328854395_8c95b19e6a6e8717108704ed26e69dff.jpg" alt="" />
          
          {menuItems.map((item, index) => (
            <Link
              key={index}
              to={item.path}
              className={`p-3 rounded-xl hover:bg-blue-50 text-gray-500 hover:text-blue-600 ${
                location.pathname === item.path ? 'bg-blue-50 text-blue-600' : ''
              }`}
            >
              {item.icon}
            </Link>
          ))}
       
        </div>
      </div>

      {/* Main Content */}
      <div className="flex-1 overflow-auto">
        <div className="p-8">
          {children}
        </div>
      </div>
    </div>
  );
}

export default Layout;